.brand-slider {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 2rem; }
  .brand-slider_back {
    display: flex;
    overflow: visible;
    align-items: center;
    font-size: 3rem;
    transform: translateX(7rem);
    cursor: pointer; }
    .brand-slider_back::before {
      content: "";
      z-index: -1;
      position: absolute;
      overflow-x: visible;
      height: 10rem;
      width: 5rem;
      background-color: rgba(218, 192, 3, 0.8);
      transform: translateX(-1rem);
      border-radius: 20%; }
    .brand-slider_back:hover::before {
      background-color: #dac003; }
  .brand-slider_continue {
    display: flex;
    overflow: visible;
    align-items: center;
    font-size: 3rem;
    transform: translateX(-2rem);
    cursor: pointer; }
    .brand-slider_continue::after {
      content: "";
      z-index: -1;
      position: absolute;
      overflow-x: visible;
      height: 10rem;
      width: 5rem;
      background-color: rgba(218, 192, 3, 0.8);
      transform: translateX(-1rem);
      border-radius: 20%; }
    .brand-slider_continue:hover::after {
      background-color: #dac003; }
  .brand-slider_title {
    display: block;
    text-align: center;
    font-size: 3rem;
    font-weight: 600; }
  .brand-slider_underline {
    display: block;
    margin-top: 1rem;
    width: 90%;
    border-bottom: 2px solid rgba(0, 0, 0, 0.2);
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1rem; }
  .brand-slider_container {
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 16fr 1fr; }
  .brand-slider_items {
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr; }
    @media (max-width: 596px) {
      .brand-slider_items {
        grid-template-columns: 1fr 1fr; } }
    .brand-slider_items_item {
      display: flex;
      flex-direction: column;
      height: 30rem;
      margin: 3rem 2rem;
      box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15); }
      .brand-slider_items_item:hover {
        box-shadow: 0 1rem 1.5rem rgba(0, 0, 0, 0.15); }
      .brand-slider_items_item_cart {
        align-self: flex-end;
        font-size: 3rem;
        cursor: pointer;
        margin: 0.8rem; }
        .brand-slider_items_item_cart:hover {
          transition: 0.2s;
          font-size: 3.5rem; }
      .brand-slider_items_item_img {
        display: flex;
        justify-content: center;
        align-content: center;
        height: 50%;
        cursor: pointer; }
        .brand-slider_items_item_img > img {
          object-fit: contain; }
      .brand-slider_items_item_bottom {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 15rem; }
        .brand-slider_items_item_bottom_title {
          display: -webkit-box;
          text-align: center;
          height: 4rem;
          margin: 1rem;
          width: 70%;
          align-self: center;
          font-size: 1.7rem;
          font-weight: 600;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
          text-overflow: ellipsis; }
        .brand-slider_items_item_bottom_price {
          height: 4rem;
          width: 100%;
          font-size: 2rem;
          padding: 0.5rem;
          color: royalblue;
          text-align: center;
          align-self: center; }
        .brand-slider_items_item_bottom_discription {
          display: -webkit-box;
          height: 3rem;
          width: 100%;
          margin: 0.5rem;
          font-size: 1.5rem;
          text-align: center;
          align-self: center;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
          text-overflow: ellipsis; }
  .brand-slider_slideout {
    animation-name: slideout;
    animation-duration: 0.5s; }
  .brand-slider_slidein {
    animation-name: slidein;
    animation-duration: 0.5s; }
  .brand-slider_slideoutRight {
    animation-name: slideoutRight;
    animation-duration: 0.5s; }
  .brand-slider_slideinRight {
    animation-name: slideinRight;
    animation-duration: 0.5s; }

@keyframes slideout {
  0% {
    opacity: 1;
    transform: translateX(0); }
  100% {
    opacity: 0;
    transform: translateX(-100%); } }

@keyframes slidein {
  0% {
    opacity: 0;
    transform: translateX(100%); }
  100% {
    opacity: 1;
    transform: translateX(0); } }

@keyframes slideoutRight {
  0% {
    opacity: 1;
    transform: translateX(0); }
  100% {
    opacity: 0;
    transform: translateX(100%); } }

@keyframes slideinRight {
  0% {
    opacity: 0;
    transform: translateX(-100%); }
  100% {
    opacity: 1;
    transform: translateX(0); } }

